.interective-map {
  &__background-block {
    width: 100vw;
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
  }

  &__background-image {
    min-width: 100%;
  }

  &__map-container {
    position: relative;
    display: flex;
    justify-content: center;
    width: fit-content;
    margin: 0 auto;
  }

  &__map-image {
    margin: 0 auto;
  }

  &__icon-block {
    width: auto;
    height: auto;
    position: absolute;
    cursor: pointer;
    display: flex;
  }

  &__icon {
    width: 30px;
    height: 30px;
    cursor: pointer;

    &_selected {
      animation-name: bounce-4;
      animation-timing-function: ease;
      align-self: flex-end;
      animation-duration: 2s;
      animation-iteration-count: infinite;
    }
  }

  @keyframes bounce-4 {
    0% {
      transform: scale(1, 1) translateY(0);
    }
    10% {
      transform: scale(1.1, 0.9) translateY(0);
    }
    30% {
      transform: scale(0.9, 1.1) translateY(-50px);
    }
    50% {
      transform: scale(1, 1) translateY(0);
    }
    100% {
      transform: scale(1, 1) translateY(0);
    }
  }
}
