@import 'styles/variables.scss';
@import 'styles/media.scss';

@keyframes vualAnim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.product-item {
  display: flex;
  flex-direction: column;
  width: 300px;
  background-color: rgba(white, 1);
  border-radius: 10px;

  &_selected {
    border: 3px solid black;
    background-color: rgba(black, 0.1);
  }

  &__image {
    width: 100%;
    height: 200px;
    object-fit: contain;
    z-index: 1;

    &_cursor {
      cursor: pointer;
    }
  }

  &__name {
    font-size: 19px;
    height: 55px;
    background-color: rgba(white, 0.7);
    border-radius: 10px 10px 0 0;
    padding: 5px 10px 5px 10px;
    font-family: Roboto;

    &_selected {
      background-color: rgba(black, 0.1);
      color: white;
    }
  }

  &__description {
    padding: 5px 10px 5px 10px;
    font-family: Roboto;

    &_selected {
      background-color: rgba(black, 0.1);
      color: white;
    }
  }

  &__image-block {
    display: flex;
    position: relative;
    margin-bottom: 10px;
  }

  &__vual {
    top: 0;
    z-index: 5;
    position: absolute;
    width: 100%;
    height: 100%;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    animation-duration: 2s;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    animation-fill-mode: forwards;

    &_show {
      animation-name: vualAnim;
      animation-duration: 2s;
    }
  }

  &__vual-btn {
    border-radius: 100px;
    width: 185px;
    height: 60px;
    border: none;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 21px;
    cursor: pointer;
  }
}
